@use '../base/variables' as v;
@use '../base/mixins' as m;

.contenido-contacto{

    @include m.tablet{
        @include m.grid(2, 5rem);
        background-image: url('../img/granos_cafe.jpg');
        background-repeat: no-repeat;
        background-position: right center;
        background-size: 40%;
        
    }
}

.formulario{
    .campo{
        margin-bottom: 2rem;
        &:last-of-type{
            margin-bottom: 0;
        }

        label{
            display: block;
            text-transform: uppercase;
            font-size: 2.2rem;
            font-weight: 700;

        }

        input:not([type="submit"]),
        select {
            width: 100%;
            padding: 1rem;
            border: .5rem solid v.$color-primario;
        }

        input[type="submit"]{
            background-color: v.$color-primario;
            width: 100%;
            padding: 1rem;
            text-transform: uppercase;
            color: v.$blanco;
            font-weight: 700;
            border: none;
            font-size: 2.2rem;

            &:hover{
                background-color: v.$color-secundario;
                cursor: pointer;
            }
        }
    }
}