@use '../base/mixins' as m;
@use '../base/variables' as v;


.testimoniales{
    padding: 10rem 2rem;

    @include m.tablet{
        padding: 10rem; 
        
     }

     .testimonial{
        background-color: v.$cafeClaro;
        padding: 2rem 4rem;
        max-width: 50rem;
        margin: 0 auto;
        border-radius: 1rem;

        .autor{
            text-align: right;
            font-size: 2.4rem;
            color: v.$color_primario;
            font-family: v.$fuente_cursiva;
            font-weight: 700;
        }
     }

}